<template>
  <v-container fluid>
    <v-card flat>
      <v-card-title>
        <v-spacer/>
        <v-text-field
          dense
          v-model="search"
          append-icon="mdi-search"
          label="Tìm kiếm"
        />
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="thesis"
        :search="search"
      >
        <template
          v-slot:top
        >
          <v-toolbar flat color="white">
            <v-card-title>
              Danh sách đồ án
            </v-card-title>
            <v-spacer/>
            <v-dialog
              persistent
              v-model="dialog"
              max-width="800px"
            >
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark class="mb-2" v-on="on">Thêm mới</v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">{{formTitle}}</span>
                </v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col>
                        <v-text-field
                          v-model="editedItem.name"
                          label="Tên đồ án "
                        />
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col>
                        <v-textarea
                          label="Mô tả"
                          v-model="editedItem.description"
                        />
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col>
                        <v-select
                          :items="subjects"
                          item-text="name"
                          item-value="_id"
                          v-model="editedItem.subject"
                          label="Môn học"
                        />
                      </v-col>
                      <v-col>
                        <v-select
                          :items="students"
                          item-text="fullName"
                          item-value="_id"
                          v-model="editedItem.student"
                          label="Sinh viên"
                        />
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col md="6">
                        <v-menu
                          v-model="menu"
                          :close-on-content-click="false"
                          :nudge-right="40"
                          transition="scale-transition"
                          offset-y
                          min-width="290px"
                        >
                          <template v-slot:activator="{ on }">
                            <v-text-field
                              :value="editedItem.deadline"
                              label="Hạn cuối"
                              readonly
                              v-on="on"
                            />
                          </template>
                          <v-date-picker
                            @input="menu = false"
                            v-model="editedItem.deadline"
                            locale="vi-VN"
                          />
                        </v-menu>
                      </v-col>
                      <v-col md="6">
                        <v-text-field
                          :disabled="!editedItem._id || !editedItem.attached"
                          label="Điểm"
                          v-model.number="editedItem.score"
                        />
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer/>
                  <v-btn
                    color="primary"
                    text
                    @click="close">
                    Hủy
                  </v-btn>
                  <v-btn
                    color="primary"
                    text
                    @click="save">
                    Lưu
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <a
            v-show="item.attached"
            :href="`http://localhost:3001/${item.attached}`"
            target="_blank"
          >
            <v-icon
              color="success"
              small
              class="mr-2"
            >
              mdi-download
            </v-icon>
          </a>
          <v-icon
            color="accent"
            small
            class="mr-2"
            @click="editItem(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            color="error"
            small
            class="mr-2"
            @click="deleteItem(item)"
          >
            mdi-trash-can-outline
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      dialog: false,
      menu: false,
      search: '',
      headers: [
        {
          text: 'Tên đồ án',
          value: 'name',
          filterable: true,
        },
        {
          text: 'Môn học',
          value: 'subject.name',
        },
        {
          text: 'Sinh viên',
          value: 'student.fullName',
        },
        {
          text: 'Điểm',
          value: 'score',
          width: '7%',
        },
        {
          value: 'action',
          sortable: false,
          align: 'end',
        },
      ],
      editedItem: {},
    };
  },
  mounted() {
    this.$store.dispatch('loadThesis');
    this.$store.dispatch('loadStudents');
    this.$store.dispatch('loadSubjects');
  },
  computed: {
    ...mapGetters(['thesis', 'students', 'subjects']),
    formTitle() {
      return this.editedItem._id ? 'Sửa thông tin đồ án' : 'Thêm thông tin đồ án';
    },
  },
  methods: {
    editItem(item) {
      this.editedItem = {
        _id: item._id,
        name: item.name,
        description: item.description,
        student: item.student._id,
        subject: item.subject._id,
        deadline: item.deadline,
        attached: item.attached,
        score: item.score,
      };
      this.dialog = true;
    },
    deleteItem(item) {
      this.$store.dispatch('deleteThesis', item);
    },
    close() {
      setTimeout(() => {
        this.editedItem = {};
      }, 300);
      this.dialog = false;
    },
    save() {
      if (this.editedItem._id) {
        this.$store.dispatch('updateThesis', this.editedItem);
      } else {
        this.$store.dispatch('createThesis', this.editedItem);
      }
      this.editedItem = {};
      this.dialog = false;
    },
  },
};
</script>
